import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { API_CONFIG } from 'src/config/config';
import { Endereco } from '../../models/endereco';
import { PedidoDTO } from '../../models/pedido.dto';
import { ItemPedidoDTO } from '../../models/item-pedido.dto';
import { RegistroDTO } from '../../models/registro.dto';
import { PedidoService } from '../../services/pedido.service';
import { RegistroService } from '../../services/registro.service';
import { StorageService } from '../../services/storage.service';
import { ProdutoService } from '../../services/produto.service';
import { MpService } from 'src/app/services/mp.service';
import { StatusPayment } from 'src/app/models/mercadopago/statusPayment';

@Component({
  selector: 'app-pedidos',
  templateUrl: './pedidos.page.html',
  styleUrls: ['./pedidos.page.scss'],
})

export class PedidosPage implements OnInit {

  registro: RegistroDTO;
  itens: PedidoDTO [] = [];
  page: number = 0;
  statusPayment: StatusPayment [] = [];

  constructor(public pedidoService: PedidoService,
    public mpService: MpService,
    public storageService: StorageService,
    public registroService: RegistroService,
    public produtoService: ProdutoService,
    public router: Router) { }

  ngOnInit() {
    let localUser = this.storageService.getLocalUser();
    if (localUser && localUser.email)
    {
      this.registroService.findByEmail(localUser.email)
      .subscribe(response=>
        { 
          this.registro = response as RegistroDTO;
          this.getPedido();
     //     this.getPedidos();
      //    this.getStatusPaymenty();
        },
       catchError =>
       {
        console.log("erro");
       });
    }
  }

  getPedido() {
      const name = '';
      this.pedidoService.searchAll(name, this.page, 12)
      .subscribe(response =>
        {
          let start = this.itens.length;
          this.itens = this.itens.concat(response['content']);
          let end = this.itens.length -1;
        },
        catchError =>                                                                                                                                                                                                                                                                                               
        {
          console.log(catchError);
        });
  }

  getPedidos(){
    this.pedidoService.findAll().subscribe(response=>
      {
        let start = this.itens.length;
        this.itens = this.itens.concat(response['content']);
        console.log(this.itens[0].itens);
        let end = this.itens.length -1;
        console.log(start);
        console.log(end);
    });  
  } 

  getStatusPaymenty(){
    this.mpService.findAll().subscribe(response=>
      {
        let start = this.statusPayment.length;
        this.statusPayment = this.statusPayment.concat(response['content']);
        let end = this.statusPayment.length -1;
    });  
  } 

  doRefresh(event) {
    this.page = 0;
    this.itens = [];
    const name = event.target.value;
    this.produtoService.searchAll(name,this.page,0)
    .subscribe(response => {
      let start = this.itens.length;
      this.itens = this.itens.concat(response['content']);  
      let end = this.itens.length -1;
    });
    this.getPedidos();
    setTimeout(() => {
      event.target.complete();
    }, 3000);
  }
}